import './seeker.scss';

let heroImg = document.querySelector('.container_center_box_img_hero') as HTMLImageElement;
let heroImgMana = document.querySelector('.box_cub_img_1') as HTMLImageElement;
let heroImgmana2 = document.querySelector('.box_cub_img_2') as HTMLImageElement;
let titleOne = document.querySelector('.title_one');
let titleTwo = document.querySelector('.title_two');
let textOne = document.querySelector('.text_one');
let textTwo = document.querySelector('.text_two');
let cubTextOne = document.querySelector('.cub_text_1');
let cubTextTwo = document.querySelector('.cub_text_2');
let hero = 'girl';
let girl_box = document.querySelector('.box_girl');
let boy_box = document.querySelector('.box_boy');
girl_box.addEventListener('click', changeHero)
/* boy_box.addEventListener('click', changeHero) */
const heroObject = {
    girl : {
        src: './src/assets/player_interface/seeker/girl.gif',
        titleOne:'Battle Mage',
        textOne:'Battle Mage is a ranged combat class, capable of dealing damage with fire and ice magic. Needs nanoparticles to use abilities',
        titleTwo:'Bio',
        textTwo:'Anna appeared as a result of the concord of two elementals from the planet Gulduar, where combat magic is practiced',
        imgMana1:'./src/assets/player_interface/seeker/mana1.png',
        imgMana2:'./src/assets/player_interface/seeker/mana2.png',
        cub_text_1:'Ice',
        cub_text_2:'Fiery',
    },
    boy : {
        src: './src/assets/player_interface/seeker/boy.gif',
        titleOne:'Warrior',
        textOne:'Warrior is a type of seeker who uses melee weapons as well as shields to hold off enemy advances',
        titleTwo:'Bio',
        textTwo:'Kaskad arrived from the planet Earth, he is the ancestor of the first people who began to explore the vastness of space',
        imgMana1:'./src/assets/player_interface/seeker/mana3.png',
        imgMana2:'./src/assets/player_interface/seeker/mana4.png',
        cub_text_1:'Warrior',
        cub_text_2:'Defender',
    }
}
function changeHero(){
     
    if(this.innerText === "Warrior"){
        if(hero === 'boy'){
            return
        }
        let box = girl_box.querySelector('.container_left_box_elms_border')
        if(box.classList.contains('container_left_box_elms_border_active')){
            box.classList.remove('container_left_box_elms_border_active')
        }
        
        boy_box.querySelector('.container_left_box_elms_border').classList.add('container_left_box_elms_border_active');
        heroImg.src = heroObject.boy.src;
        heroImg.classList.add('container_center_box_img_hero_boy')
        heroImg.classList.remove('container_center_box_img_hero')
        
        titleOne.textContent = heroObject.boy.titleOne;
        titleTwo.textContent = heroObject.boy.titleTwo;
        textOne.textContent = heroObject.boy.textOne;
        textTwo.textContent = heroObject.boy.textTwo;
        cubTextOne.textContent = heroObject.boy.cub_text_1;
        cubTextTwo.textContent = heroObject.boy.cub_text_2;
        heroImgMana.src = heroObject.boy.imgMana1;
        heroImgmana2.src = heroObject.boy.imgMana2;
        console.log('it is boy');

    }
    if(this.innerText === "Battle Mage"){
        if(hero === 'girl'){
            return
        }
        let box = boy_box.querySelector('.container_left_box_elms_border')
        if(box.classList.contains('container_left_box_elms_border_active')){
            box.classList.remove('container_left_box_elms_border_active')
        }
        
        girl_box.querySelector('.container_left_box_elms_border').classList.add('container_left_box_elms_border_active');
        heroImg.src = heroObject.girl.src;
        heroImg.classList.add('container_center_box_img_hero')
        heroImg.classList.remove('container_center_box_img_hero_boy')
        titleOne.textContent = heroObject.girl.titleOne;
        titleTwo.textContent = heroObject.girl.titleTwo;
        textOne.textContent = heroObject.girl.textOne;
        textTwo.textContent = heroObject.girl.textTwo;
        cubTextOne.textContent = heroObject.girl.cub_text_1;
        cubTextTwo.textContent = heroObject.girl.cub_text_2;
        heroImgMana.src = heroObject.girl.imgMana1;
        heroImgmana2.src = heroObject.girl.imgMana2;
        console.log('it is girl');

    }
    if(hero === 'girl'){
       
       return hero ='boy'
    }
    if(hero === 'boy'){
       
      return  hero ='girl'
    }
}
function submitSeekerForm(event: Event){
    event.preventDefault();
    console.log(hero);
    localStorage.setItem('hero', hero);
    window.location.href = "game.html";
} 
document.querySelector('.form_seeker').addEventListener('submit', (event)=> submitSeekerForm(event));